* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.singleItem {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.country-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
}

.flag {
  width: 100px;
  height: 60px;
}

.countryName {
  font-size: 21px;
  font-weight: bold;
}

.singleCountryName {
  font-weight: bold;
}

.listInDescription {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}

.valueOfDescs {
  font-size: 20px;
  font-weight: 500;
}
